import {CenterActionTypes} from "./center.types";

const INITIAL_STATE = {
    centers: [],
    nearby_centers: [],
    is_centers_updated: false,
    tracking_centers: [],
}

const centerReducer = (state = INITIAL_STATE, action = "") => {
    switch (action.type) {
        case CenterActionTypes.SET_CENTERS:
            return {
                ...state,
                centers: action.payload,
                is_centers_updated: true
            };
        case CenterActionTypes.SET_NEARBY_CENTERS:
            return {
                ...state,
                nearby_centers: action.payload,
            };
        case CenterActionTypes.SET_TRACKING_CENTER:
            return {
                ...state,
                tracking_centers: action.payload
            }
        case CenterActionTypes.CENTER_UPDATE_DONE:
            return {
                ...state,
                is_centers_updated: action.payload
            }
        default:
            return state;
    }
}
export default centerReducer;