import {Button} from "react-bootstrap";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";

export const WhatsAppButtonRender = (renderProps) => (
    <Button onClick={renderProps.onClick} disabled={renderProps.disabled} variant={"primary"} className={"shadow my-1"}>
        <FontAwesomeIcon icon={'comment-dots'} size={"3x"} />
        <div>
            {
                renderProps.text ||
                "Login and subscribe SMS notification"
            }
        </div>
    </Button>
)