import React, {Component} from 'react';
import {Button, Col, Row} from "react-bootstrap";
import {connect} from "react-redux";
import config from "../../config.json";
import BackendUrls from "../../constants/BackendUrls.json";
import axios from "axios";
import GoogleAnalyticsEventTracker from "../google/analytics/GoogleAnalyticsEventTracker";

class Footer extends Component {
    state = {
        is_valid_feedback: false,
        is_feedback_btn_disabled: true,
        feedback_text:""
    }

    GaEventTracker = GoogleAnalyticsEventTracker();

    constructor(props) {
        super(props);
        this.OnSubmitFeedback = this.OnSubmitFeedback.bind(this);
        this.OnChangeFeedback = this.OnChangeFeedback.bind(this);
    }

    OnSubmitFeedback(){
        this.setState({
            is_valid_feedback: true
        });

        const url = config.BACKEND_BASE_URL + BackendUrls.USER_FEEDBACK;
        const options = {
            headers: {
                'Content-Type': 'application/json',
                'Authorization': 'Token ' + this.props.user.token
            }
        };

        axios.post(url, {feedback:this.state.feedback_text}, options)
            .then(res => {
                console.debug(res);
            })
            .catch(error => {
                this.setState({
                    is_valid_feedback: false
                });
                this.GaEventTracker("Failed Feedback", error);
                console.log(error);
            });
    }

    OnChangeFeedback(event){
        let text = event.target.value;
        this.setState({
            feedback_text: text,
            is_feedback_btn_disabled: text.length < 10
        })
    }

    render() {
        return (
            <section className="p-0">
                <div className="shadow bg-white p-4 my-2 rounded-lg">
                    <Row>
                       <Col>
                           <div className="font-weight-bold">Contact</div>
                           <a href="https://www.linkedin.com/feed/update/urn%3Ali%3Aactivity%3A6798913715489120256/" target="_blank" rel={"noreferrer"} className="no-text-decor">
                               <div className="text-primary" style={{fontWeight: 800, fontSize: 20}}>
                                   Linked <span className="bg-primary text-white rounded-sm" style={{padding: '1px 2px 1px 2px'}}>in</span>
                               </div>
                           </a>
                       </Col>
                        {
                            this.props.user.is_server_up &&
                            <Col>
                                <div className="font-weight-bold">Feedback</div>
                                <div>
                                <textarea className={"form-control no-input-shadow"}
                                          onChange={this.OnChangeFeedback}
                                          value={this.state.feedback_text}
                                          disabled={this.state.is_valid_feedback}/>
                                    {
                                        this.state.is_valid_feedback ? <div>Thanks for the feedback :) </div> :
                                            <Button variant={"primary"} className="my-1 float-right"
                                                    onClick={this.OnSubmitFeedback}
                                                    disabled={this.state.is_feedback_btn_disabled}>
                                                Submit
                                            </Button>
                                    }
                                </div>
                            </Col>
                        }
                    </Row>
                </div>
            </section>
        );
    }
}

const mapStateToProps = ({ user }) => (
    {
        user: user
    }
)

const mapDispatchToProps = () => ({})

export default connect(mapStateToProps, mapDispatchToProps) (Footer);