import React, {Component} from 'react';
import GoogleLogin from "react-google-login";
import config from "../../config.json";
import BackendUrls from "../../constants/BackendUrls.json";
import {WhatsAppButtonRender} from "./WhatsAppButtonRender";
import axios from "axios";
import {setSubscriptions, setToken} from "../../../../redux/user/user.actions";
import {connect} from "react-redux";
import WhatsAppSubscribeManager from "./WhatsAppSubscribeManager";
import {setTrackingCenters} from "../../../../redux/centers/center.actions";
import GoogleAnalyticsEventTracker from "./analytics/GoogleAnalyticsEventTracker";

class GoogleLoginButton extends Component {

    state = {
        is_google_login_supported: true,
    }

    GaEventTracker = GoogleAnalyticsEventTracker();

    constructor(props) {
        super(props);
        this.responseGoogle = this.responseGoogle.bind(this);
        this.exchangeToken = this.exchangeToken.bind(this);
    }

    exchangeToken(accessToken){
        const options = {
            headers: {
                'Content-type': 'application/json'
            }
        };

        axios.post(config.BACKEND_BASE_URL + BackendUrls.GET_TOKEN_URL, {"access_token": accessToken}, options)
            .then(res => {
                this.props.setToken(res.data["token"]);
                this.props.getUser();
            })
            .catch(error => {
                this.GaEventTracker("Fail GET User", error);
                console.log(error);
            });
    }

    responseGoogle = (response) => {
        this.GaEventTracker("AUTH 2.0 success");
        if(response.error === "idpiframe_initialization_failed"){
            this.setState({
                is_google_login_supported: false
            })
        }

        if(response.accessToken) {
            this.exchangeToken(response.accessToken);
        }
    }

    render() {
        if(this.state.is_google_login_supported) {
            if (!this.props.user.token) {
                return (
                    <GoogleLogin
                        clientId={config.GMAIL_API_CLIENT_ID}
                        render={WhatsAppButtonRender}
                        buttonText="Login"
                        onSuccess={this.responseGoogle}
                        onFailure={this.responseGoogle}
                        cookiePolicy={'single_host_origin'}
                        isSignedIn={true}
                    />
                );
            } else {
                return <WhatsAppSubscribeManager callback={this.props.onSubscriptionResponseCallback} />
            }
        }
        else{
            return (
                <div className="bg-white rounded-lg p-2 red-shadow btn my-1">
                    Google Login not supported, please try another browser or close icognito mode.
                </div>
            );
        }
    }
}


const mapStateToProps = ({ user,vaccine }) => (
    {
        user: user,
        vaccine: vaccine
    }
)

const mapDispatchToProps = dispatch => ({
    setToken: token => dispatch(setToken(token)),
    setSubscriptions: subscriptions => dispatch(setSubscriptions(subscriptions)),
    setTrackingCenter: centers => dispatch(setTrackingCenters(centers))
})

export default connect(mapStateToProps, mapDispatchToProps) (GoogleLoginButton);
