import {PositionActionTypes} from "./position.types";


const INITIAL_STATE = {
    latitude: 100,
    longitude: 157
}

const positionReducer = (state = INITIAL_STATE, action) => {
    switch (action.type) {
        case PositionActionTypes.SET_LATITUDE:
            return {
                ...state,
                latitude: action.payload
            };
        case PositionActionTypes.SET_LONGITUDE:
            return {
                ...state,
                longitude: action.payload
            };
        default:
            return state;
    }
}

export default positionReducer