import { CenterActionTypes } from "./center.types";

export const setCenters = centers => (
    {
        type: CenterActionTypes.SET_CENTERS,
        payload: centers
    }
)

export const setNearbyCenter = centers => (
    {
        type: CenterActionTypes.SET_NEARBY_CENTERS,
        payload: centers
    }
)

export const setTrackingCenters = centers => (
    {
        type: CenterActionTypes.SET_TRACKING_CENTER,
        payload: centers
    }
)

export const setCentersUpdateDone = () => (
    {
        type: CenterActionTypes.CENTER_UPDATE_DONE,
        is_centers_updated: false
    }
)