import React, {Component} from 'react';
import {Button} from "react-bootstrap";
import {connect} from "react-redux";
import {setTrackingCenters} from "../../../../redux/centers/center.actions";

class TrackingPinCodes extends Component {
    componentDidMount() {
        // this.SetTrackingCenters();
    }

    render() {
        return (
            <div className={"shadow p-1 rounded-lg bg-white my-1"}>
                {
                    this.props.vaccine.tracking_centers && this.props.vaccine.tracking_centers.length > 0?
                        <h6 className={"font-weight-bold btn"}> Tracking pin codes 18-45</h6>:
                        <h6 className={"font-weight-bold btn"}> No notification trackers </h6>
                }
                <hr />
                {
                    this.props.vaccine.tracking_centers && this.props.vaccine.tracking_centers.map(t_center =>{
                        return (
                            <div className={"rounded p-2 m-1 font-weight-bold bg-cowin2 position-relative overflow-hidden"} key={"center-tr-" + t_center.pin_code}>
                                {t_center.pin_code}
                                <Button variant={"danger"} onClick={() => {
                                    this.props.removeFromTracking(t_center.pin_code)
                                }} className={"float-right  position-absolute rounded-0"} style={{top:0, right:0, height:"100%"}}> X </Button>
                            </div>
                        )
                    })
                }
            </div>
        );
    }
}


const mapStateToProps = ({ user, vaccine }) => (
    {
        user: user,
        vaccine: vaccine
    }
)

const mapDispatchToProps = dispatch => ({
    setTrackingCenters: centers => dispatch(setTrackingCenters(centers))
})

export default connect(mapStateToProps, mapDispatchToProps) (TrackingPinCodes);
