import {UserActionTypes} from "./user.types";

const INITIAL_STATE = {
    is_server_up:true,
    token:null,
    subscriptions:[]
}

const userReducer = (state = INITIAL_STATE, action) => {
    switch (action.type) {
        case UserActionTypes.SET_USER_TOKEN:
            return {
                ...state,
                token: action.payload
            };
        case UserActionTypes.SET_SERVER_STAT:
            return {
                ...state,
                is_server_up: action.payload
            };
        case UserActionTypes.SET_USER_SUBSCRIPTION:
            return {
                ...state,
                subscriptions: action.payload
            };
        default:
            return state;
    }
}
export default userReducer;