import React, {Component} from 'react';
import GoogleMapReact from 'google-map-react';
import {Badge, Button, Col, Row} from "react-bootstrap";
import Constant from "../../constants/Constant.json";
import $ from 'jquery';
import moment from "moment";
import GoogleAnalyticsEventTracker from "../google/analytics/GoogleAnalyticsEventTracker";
import GoogleLoginButton from "./GoogleLoginButton";
import {setToken} from "../../../../redux/user/user.actions";
import {connect} from "react-redux";
import {setCentersUpdateDone, setNearbyCenter, setTrackingCenters} from "../../../../redux/centers/center.actions";
import config from "../../config.json";
import mapImage from "../../static/images/map.jpg";
import {GetCenters} from "../vaccinesessions/CowinRequestManager";
import CUrl from "../../constants/CowinUrl.json";

class GoogleMapComponent extends Component {

    state = {
        my_position: {lat: 12.97, lng: 77.59},
        map_center: {lat: 12.97, lng: 77.59},
        map_zoom: 12,
        maps_data: {},
        show_map: false,
        markers: [],
    }

    GaEventTracker = GoogleAnalyticsEventTracker();

    constructor(props) {
        super(props);
        this.OnClickCenterBox = this.OnClickCenterBox.bind(this);
        this.LocateMyPosition = this.LocateMyPosition.bind(this);
        this.OnGetNearbyCenterCallback = this.OnGetNearbyCenterCallback.bind(this);
        this.DrawMarkersOnMap = this.DrawMarkersOnMap.bind(this);
        this.GetNumber = this.GetNumber.bind(this);
        this.SetMyPosition = this.SetMyPosition.bind(this);
    }

    componentDidMount() {
        navigator.geolocation.getCurrentPosition((position) => {
            this.setState({
                my_position: { lat:position.coords.latitude, lng: position.coords.longitude},
                map_center: { lat:position.coords.latitude, lng: position.coords.longitude},
            });
            this.SubmitLatLong(position.coords);
        });
    }

    SubmitLatLong(coords){
        if(!coords) return;
        GetCenters(config.COWIN_BASE_URL + CUrl.CENTERS_BY_LAT_LONG.replace("<LAT>", coords.latitude).replace("<LONG>", coords.longitude), this.OnGetNearbyCenterCallback);
    }

    OnGetNearbyCenterCallback(centers){
        this.props.setNearbyCenter(centers);
        this.GaEventTracker("Received Nearby Centers");
        this.setState({
            show_map: true
        })
        this.DrawMarkersOnMap();
    }

    // RemoveAllMarkersFromMap(){
    //     this.state.markers.forEach(marker => {
    //         marker.setMap(null);
    //     })
    //     this.setState({
    //         markers: []
    //     })
    // }

    LocateMyPosition(){
        this.setState({
            map_center: this.state.my_position,
            map_zoom: 13,
        })
    }

    HandleApiLoaded = (map, maps) => {
        this.setState({
            maps_data: {
                map: map,
                maps: maps
            }
        });
    }

    DrawMarkersOnMap(){
        if(!this.state.maps_data.map) {
            setTimeout(this.DrawMarkersOnMap, 500);
            return;
        }
        this.SetMyPosition();
        let map = this.state.maps_data.map;
        this.props.vaccine.nearby_centers.forEach(it => {
            let position = { lat: this.GetNumber(it.lat), lng: this.GetNumber(it.long)};
            let marker = new this.state.maps_data.maps.Marker({
                position: position,
                // animation: maps.Animation.DROP,
                map,
                // icon: {
                //     url: "https://atlas-content-cdn.pixelsquid.com/stock-images/hospital-3yL2QM6-600.jpg",
                //     size: maps.Size(10, 10),
                // },
                title: it.name,
                label: {
                    text:"H",
                    color: "white",
                    fontWeight: "bold",
                }
            })

            marker.addListener("click", () => {
                marker.setAnimation(this.state.maps_data.maps.Animation.BOUNCE);
                setTimeout(()=>{marker.setAnimation(null);}, 2000);
            });

            marker.addListener("mouseover", () => {
                const child_parent = $("#center-" + it.center_id).parent();
                const scroller = $(".center-parent");
                let offset = child_parent.offset().top + scroller.scrollTop() - child_parent.parent().offset().top;
                scroller.animate({scrollTop:offset}, 500);
                this.SetActiveCenterBox(it.center_id);
            })
            this.state.markers.push(marker);
        })
    }

    SetMyPosition() {
        const maps = this.state.maps_data.maps;
        const map = this.state.maps_data.map;
        let marker = new this.state.maps_data.maps.Marker({
            position: this.state.my_position,
            animation: maps.Animation.DROP,
            map,
            icon: {
                // url: "https://www.clipartmax.com/png/full/219-2193698_white-button-clipart.png",
                url: "https://w7.pngwing.com/pngs/81/570/png-transparent-profile-logo-computer-icons-user-user-blue-heroes-logo-thumbnail.png",
                scaledSize: new maps.Size(16, 16),
            },
            title: "Me",
        });

        marker.addListener("click", () => {
            marker.setAnimation(this.state.maps_data.maps.Animation.BOUNCE);
            setTimeout(()=>{marker.setAnimation(null);}, 2000);
        });
    }

    GetNumber(strNum){
        return Number(parseFloat(strNum).toFixed(5));
    }

    SetDirection(center) {
        this.GaEventTracker("Google map directions requests")
        let directionsService = new this.state.maps_data.maps.DirectionsService();
        let directionsRenderer = new this.state.maps_data.maps.DirectionsRenderer();
        directionsRenderer.setMap(this.state.maps_data.map);
        let destination = { lat: this.GetNumber(center.lat), lng: this.GetNumber(center.long)}

        let request = {
            origin: this.state.my_position,
            destination: destination,
            travelMode: this.state.maps_data.maps.TravelMode.DRIVING,
        };

        directionsService.route(request, function(response, status) {
            if (status === 'OK') {
                directionsRenderer.setDirections(response);
            }
        });
    }

    SetActiveCenterBox(center_id) {
        $('.center-box').removeClass('border-primary blue-shadow');
        $('#center-'+center_id).addClass('border-primary blue-shadow');
    }

    OnClickCenterBox(center){
        this.SetActiveCenterBox(center.center_id);
        let position = {lat: parseFloat(center.lat), lng: parseFloat(center.long)};
        this.setState({
            map_center: position,
            map_zoom: 14,
        })
    }

    GetFormattedDate(date){
        return moment(date, "DD-MM-YYYY").format("DD MMM");
    }

    render() {
        return (
            <section>
                <Row>
                    <Col md={9} className={""}>
                        {
                            (this.state.show_map > 0)?
                            <div style={{height: 600, width: "100%"}} className={"shadow position-relative rounded-lg overflow-hidden"}>
                                <GoogleMapReact
                                    bootstrapURLKeys={{key: config.GOOGLE_MAP_API, language: "en"}}
                                    defaultCenter={{lat: 12.97, lng: 77.59}}
                                    center={this.state.map_center}
                                    defaultZoom={10}
                                    zoom={this.state.map_zoom}
                                    yesIWantToUseGoogleMapApiInternals={true}
                                    onGoogleApiLoaded={({map, maps}) => this.HandleApiLoaded(map, maps)}
                                    onChildMouseEnter={this.OnMouseHoverOnHospital}
                                >
                                    {/*<MyLocationComponent*/}
                                    {/*    lat={this.state.my_position.lat}*/}
                                    {/*    lng={this.state.my_position.lng}*/}
                                    {/*/>*/}
                                </GoogleMapReact>
                                <Button variant={"light"} className="my-location position-absolute shadow" style={{right: 10, bottom:150, height: 38, width:38, borderRadius: 38}} onClick={this.LocateMyPosition}>
                                    +
                                </Button>
                            </div>: <div style={{height: 600, width: "100%"}} className={"shadow position-relative rounded-lg overflow-hidden"}>
                                <img src={mapImage} height="100%"  alt={"map"}/>
                                <div style={{height: "100%", width: "100%", top: 0, background: "rgba(0,0,0, 0.5)",}} className="p-2 position-absolute text-white font-weight-bold d-flex justify-content-center align-items-center">
                                    Allow location, to get centers nearby you
                                </div>
                            </div>
                        }
                    </Col>
                    <Col md={3} style={{maxHeight: 600, overflowY:"auto"}} className="center-parent">
                        {
                            this.props.user.is_server_up && !(this.props.user.subscriptions && this.props.user.subscriptions.includes(Constant.SUBSCRIPTION_WA)) &&
                            <GoogleLoginButton getUser={this.props.getUser} onSubscriptionResponseCallback={this.props.onSubscriptionResponseCallback}/>
                        }
                        {
                            (this.props.vaccine.nearby_centers.length === 0) && <Col className={"p-0 py-1"}>
                                <div className={"p-2 shadow rounded-lg border center-box bg-white btn font-weight-bold my-1"} style={{width: "100%"}}>
                                    No centers are available now
                                </div>
                            </Col>
                        }
                        {
                            this.props.vaccine.nearby_centers.map(center =>
                                <Col className={"p-0 py-1"} key={"center-" + center.center_id}>
                                    <div className={"p-1 shadow rounded-lg border center-box bg-white"}
                                         id={"center-" + center.center_id} onClick={() => {
                                        this.OnClickCenterBox(center)
                                    }}>
                                        <b>{center.name}</b>
                                        <p className={"text-secondary small m-0"}>{center.location}, {center.block_name}</p>
                                        <p className={"text-secondary small m-0"}>{center.district_name}, {center.state_name}, {center.pincode}</p>
                                        <div className={"text-primary btn px-0 small"} onClick={() => {this.SetDirection(center)}}
                                             style={{fontSize:11}}>
                                            Show direction on map
                                        </div>
                                        <div>
                                            {
                                                this.props.isTrackingContains(Number(center.pincode)) ?
                                                    "" :
                                                    <Badge variant={"primary"} className={"btn p-1 m-1"} onClick={() => {
                                                        this.props.addToTracking(Number(center.pincode))
                                                    }}> Set Tracking</Badge>
                                            }
                                        </div>
                                    </div>
                                </Col>
                            )
                        }
                    </Col>
                </Row>

                {/*<TrackModal*/}
                {/*    show={this.state.modalShow}*/}
                {/*    onHide={() => this.SetModalShow(false)}*/}
                {/*/>*/}
            </section>
        );
    }
}

const mapStateToProps = ({ user, vaccine }) => (
    {
        user: user,
        vaccine: vaccine
    }
)

const mapDispatchToProps = dispatch => ({
    setToken: token => dispatch(setToken(token)),
    setNearbyCenter: centers => dispatch(setNearbyCenter(centers)),
    setTrackingCenters: centers => dispatch(setTrackingCenters(centers)),
    setCentersUpdateDone: () => dispatch(setCentersUpdateDone())
})

export default connect(mapStateToProps, mapDispatchToProps) (GoogleMapComponent);
