import { useIndexedDB } from 'react-indexed-db';
import GoogleAnalyticsEventTracker  from "../google/analytics/GoogleAnalyticsEventTracker";
import config from "../../config.json";

const DBConfig = {
    name: 'cowin',
    version: config.DB_VERSION,
    objectStoresMeta: [
        {
            store: 'centers',
            storeConfig: { keyPath: 'pin_code'},
            storeSchema: [
                { name: 'pin_code', keypath: 'pin_code', options: { unique: true } },
            ]
        }
    ]
}

const GaEventTracker = GoogleAnalyticsEventTracker();

function RemoveAllCenters(){
    const { clear } = useIndexedDB('centers');
    return clear();
}

function GetAllCenters() {
    const { getAll } = useIndexedDB('centers');
    return getAll();
}

function AddCenter(pinCode) {
    GaEventTracker("[SW] Center Added", pinCode);
    const { add } = useIndexedDB('centers');
    return add({
                pin_code: pinCode
            });
}

function RemoveCenter(pinCode) {
    const { deleteRecord  } = useIndexedDB('centers');
    GetAllCenters().then(item=>{
        item.forEach(it=> {
                if (it.pin_code === pinCode) {
                    return deleteRecord(it.pin_code);
                }
            }
        )
    })
}

export {AddCenter, GetAllCenters, RemoveCenter, DBConfig, RemoveAllCenters};