import React, {Component} from 'react';
import {Badge, Button, Table} from "react-bootstrap";
import moment from "moment";
import Constant from "../../constants/Constant.json";
import {setToken} from "../../../../redux/user/user.actions";
import {connect} from "react-redux";

class VaccineSessionTable extends Component {
    state = {
        week: [0 ,1, 2, 3, 4, 5, 6].map(it=> moment().add(it, 'day').format("DD-MM-YY"))
    }

    OnChangeDate(date){
        this.setState({
            week: [0 ,1, 2, 3, 4, 5, 6].map(it=> moment(date, 'YYYY-MM-DD').add(it, 'day').format("DD-MM-YY"))
        })
    }

    render() {
        return (
            <div className={"bg-white shadow rounded-lg mt-2 overflow-hidden p-0"}>
                {
                    this.props.vaccine.centers.length > 0 &&
                    <Table striped bordered hover>
                        <thead>
                        <tr>
                            <th>Name</th>
                            {
                                this.state.week.map(day =>
                                    <th key={"th-" + day}>{day}</th>
                                )
                            }
                        </tr>
                        </thead>
                        <tbody>
                        {
                            this.props.vaccine.centers.map(it =>
                                <tr key={it.center_id}>
                                    <td>
                                        <div>
                                            <div className={"font-weight-bolder"}>
                                                {it.name}
                                                <sup className={"small"}>
                                                    {' '}<Badge variant="secondary">{it.fee_type}</Badge>
                                                </sup>
                                            </div>
                                            <p className={"text-secondary small m-0"}>{it.address}, {it.block_name}</p>
                                            <p className={"text-secondary small m-0"}>{it.district_name}, {it.state_name}, {it.pincode}</p>
                                        </div>
                                    </td>
                                    {
                                        this.state.week.map(date => {
                                            let index = it.sessions.findIndex(session => session.date === moment(date, 'DD-MM-YY').format('DD-MM-YYYY'));
                                            if (index >= 0) {
                                                return (
                                                    <td key={it.sessions[index].session_id}>
                                                        <div>
                                                            <span className={""}>{it.sessions[index].vaccine}</span>
                                                            <p className={"small m-0 text-danger"}>For {it.sessions[index].min_age_limit}+</p>
                                                            {
                                                                it.sessions[index].available_capacity ?
                                                                        <a href={Constant.COWIN_REGISTRATION}
                                                                           target="_blank" rel="noreferrer">
                                                                            <Button variant={"success p-1"}
                                                                                    data-toggle="tooltip"
                                                                                    data-placement="top"
                                                                                    title="Tooltip on top">
                                                                                Book <sup><Badge
                                                                                variant={"primary"}>{it.sessions[index].available_capacity}</Badge></sup>
                                                                            </Button>
                                                                        </a> :
                                                                    <Button variant={"danger p-0"}>Booked</Button>
                                                            }
                                                        </div>
                                                    </td>
                                                );
                                            } else {
                                                return (<td key={Math.random()}/>);
                                            }
                                        })
                                    }
                                </tr>
                            )
                        }
                        </tbody>
                    </Table>
                }
                {
                    this.props.vaccine.centers.length <=0 && <div className="text-center font-weight-bold p-4">No centers are available for now, try to search for another pin code.</div>
                }
            </div>
        );
    }
}

const mapStateToProps = ({ user, vaccine }) => (
    {
        user: user,
        vaccine: vaccine
    }
)

const mapDispatchToProps = dispatch => ({
    setToken: token => dispatch(setToken(token))
})

export default connect(mapStateToProps, mapDispatchToProps) (VaccineSessionTable);
