import React, {Component} from 'react';
import {WhatsAppButtonRender} from "./WhatsAppButtonRender";
import {setSubscriptions} from "../../../../redux/user/user.actions";
import {connect} from "react-redux";
import config from "../../config.json";
import BackendUrls from "../../constants/BackendUrls.json";
import axios from "axios";
import Constant from "../../constants/Constant.json";
import {Button, FormControl} from "react-bootstrap";

class WhatsAppSubscribeManager extends Component {
    state={
        mobile_number: "",
        is_valid_mobile: false,
        show_mobile_input: false,
        submitting_mobile: false
    }

    options = {
        headers: {
            'Content-Type': 'application/json',
            'Authorization': 'Token ' + this.props.user.token
        }
    }

    constructor(props) {
        super(props);
        this.OnWaSubscriptionBtnToggle = this.OnWaSubscriptionBtnToggle.bind(this);
        this.OnMobileChange = this.OnMobileChange.bind(this);
        this.OnSubmitMobile = this.OnSubmitMobile.bind(this);
    }

    IsSubscriptionContainsWa(){
        return this.props.user.subscriptions && this.props.user.subscriptions.includes(Constant.SUBSCRIPTION_WA);
    }

    OnSubmitMobile() {
        if (this.IsSubscriptionContainsWa()) return;
        this.setState({
            submitting_mobile: true
        })

        const payload = {
            'subscription': Constant.SUBSCRIPTION_WA,
            'mobile': this.state.mobile_number
        }
        axios.post(config.BACKEND_BASE_URL + BackendUrls.NOTIFICATION_SUBSCRIBED_URL, payload, this.options)
            .then(res => {
                if (res.data["status"] === "success" || res.data["message"] === "you already subscribed") {
                    let subscription = this.props.user.subscriptions;
                    this.props.callback && this.props.callback(res.data["message"]);
                    if(subscription) {
                        subscription.push(Constant.SUBSCRIPTION_WA);
                        this.props.setSubscriptions(subscription);
                    }
                }else {
                    this.setState({
                        submitting_mobile: false,
                        show_mobile_input: false
                    })
                }
            })
            .catch(error => {
                console.log(error);
                this.setState({
                    submitting_mobile: false
                })
            });
    }

    OnWaSubscriptionBtnToggle(){
        if(this.IsSubscriptionContainsWa()){
            const payload = {
                'subscription': Constant.SUBSCRIPTION_WA,
            }

            axios.post(config.BACKEND_BASE_URL + BackendUrls.NOTIFICATION_UNSUBSCRIBED_URL, payload, this.options)
                .then(res => {
                    if (res.data["status"] === "success") {
                        let subscription = this.props.user.subscriptions;
                        if(subscription) {
                            subscription[subscription.indexOf(Constant.SUBSCRIPTION_WA)] = subscription[subscription.length - 1]
                            subscription.pop();
                            this.props.setSubscriptions(subscription);
                        }
                    }
                })
                .catch(error => {
                    console.log(error);
                });
        }
        else {
            this.setState({
                show_mobile_input: true
            })
        }
    }

    OnMobileChange(event){
        let number = event.target.value.replace(/^0+/, '')
        this.setState({
            mobile_number: number,
            is_valid_mobile: event.target.value.length === 10
        })
    }

    GetButton = () => {
        if(this.props.user.subscriptions && this.props.user.subscriptions.includes(Constant.SUBSCRIPTION_WA))
        {
            return (<WhatsAppButtonRender text={"Unsubscribe text SMS notification"}
                                  onClick={this.OnWaSubscriptionBtnToggle}/>);
        }
        return (<WhatsAppButtonRender text={"Subscribe text SMS notification"} onClick={this.OnWaSubscriptionBtnToggle}/>);
    }

    render() {
        return (
            <div>
                {
                    this.state.show_mobile_input?
                        <div className={"shadow rounded-lg p-1 border-primary border"}>
                            <FormControl className={"no-input-shadow"}
                                         placeholder={"Enter mobile number"}
                                         type={"number"}
                                         value={this.state.mobile_number}
                                         onChange={this.OnMobileChange}
                                         disabled={this.state.submitting_mobile}
                            />
                            <Button varient={"primary"} style={{width: "100%"}} className={"mt-2"} disabled={!this.state.is_valid_mobile || this.state.submitting_mobile} onClick={this.OnSubmitMobile}> Subscribe SMS </Button>
                        </div>: <this.GetButton />
                }
            </div>
        );
    }
}


const mapStateToProps = ({ user }) => (
    {
        user: user
    }
)

const mapDispatchToProps = dispatch => ({
    setSubscriptions: subscriptions => dispatch(setSubscriptions(subscriptions)),
})

export default connect(mapStateToProps, mapDispatchToProps) (WhatsAppSubscribeManager);
