import React from 'react';
import ReactDOM from 'react-dom';
import * as serviceWorkerRegistration from './serviceWorkerRegistration';
import reportWebVitals from './reportWebVitals';

import 'bootstrap/dist/css/bootstrap.min.css';
import './index.css';
import App from './main/cowin/components/app/App';

import { BrowserRouter } from "react-router-dom";
import { Provider } from 'react-redux';
import {store, persist_store} from './redux/store';
import { PersistGate } from 'redux-persist/integration/react';
import { library } from '@fortawesome/fontawesome-svg-core';
import { fab, faWhatsapp } from '@fortawesome/free-brands-svg-icons';
import { faUser, faCoffee, faAddressBook,
    faLocationArrow, faSearchLocation,
    faEdit, faTrash, faClinicMedical,faCommentDots } from '@fortawesome/free-solid-svg-icons';

library.add(
    fab,
    faUser,
    faCoffee,
    faAddressBook,
    faLocationArrow,
    faSearchLocation,
    faEdit,
    faTrash,
    faClinicMedical,
    faWhatsapp,
    faCommentDots
)

ReactDOM.render(
  <React.StrictMode>
      <Provider store={store}>
          <PersistGate loading={null} persistor={persist_store}>
              <BrowserRouter/>
              <App/>
          </PersistGate>
      </Provider>
  </React.StrictMode>,
  document.getElementById('root')
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorkerRegistration.register();

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
